
.wrapper {
  width: 960px;
  margin: 0 auto;
}
.content {
  height: 3000px;
}
.header {
  background: #2f93ce;
  color: #fff;
  padding: 10px 20px;
  margin: 0;
  width: 100%;
}
.headerBox {
  height: 465px;
  padding: 0;
}
.sidebar {
  float: left;
  width: 230px;
  padding: 10px 15px;
}
.article {
   width: 458px;
   padding: 0 20px;
   border-left: 1px solid #ccc;
   border-right: 1px solid #ccc;
   float: left;
}
.articleBox {
  height: 900px;
}
.leftColumn {
  height: 2200px;
}
.rightColumn {
  height: 2540px;
}
.stopPoint {
  height: 500px;
  background: rgb(103, 104, 107);
  color: #fff;
}



